        <div class="cs-t3 txt-indent55">1.5.5 使用 ES6,7</div>
        <div class="cs-t4 txt-indent95">必须优先使用 ES6,7 中新增的语法糖和函数。这将简化你的程序，并让你的代码更加灵活和可复用。</div>
