        <div class="cs-t3 txt-indent55">1.5.6 括号</div>
        <div class="cs-t4 txt-indent95">下列关键字后必须有大括号（即使代码块的内容只有一行）：if, else, for, while, do, switch, try, catch, finally, with。</div>
