        <div class="cs-t1">二、Vue 项目规范</div>
        <div class="cs-t2 txt-indent20">(一) Vue 编码基础</div>
        <div class="cs-t4 txt-indent95">vue 项目规范以 Vue 官方规范 （https://cn.vuejs.org/v2/style-guide/） 中的规范为基础，在其上面进行项目开发，故所有代码均遵守该规范。</div>
        <div class="cs-t4 txt-indent95">请仔仔细细阅读 Vue 官方规范。</div>
        <div class="cs-t3 txt-indent55">2.1.1. 组件规范</div>
        <div class="cs-t4 txt-indent95">1) 组件名为多个单词。</div>
        <div class="cs-t4 txt-indent95">组件名应该始终是多个单词组成（大于等于 2），且命名规范为KebabCase格式。</div>
        <div class="cs-t4 txt-indent95">这样做可以避免跟现有的以及未来的 HTML 元素相冲突，因为所有的 HTML 元素名称都是单个单词的。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    export default {
                      name: 'TodoItem'
                      // ...
                    };
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code>
                    export default {
                      name: 'Todo',
                      // ...
                    }
                    export default {
                      name: 'todo-item',
                      // ...
                    }
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">2) 组件文件名为 PascalCase 格式</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |- MyComponent.vue
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |- myComponent.vue
                        |- MyComponent.vue
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">3) 基础组件文件名为 Base 开头，使用完整单词而不是缩写。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |- BaseButton.vue
                        |- BaseTable.vue
                        |- BaseIcon.vue
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |- MyButton.vue
                        |- VueTable.vue
                        |- Icon.vue
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">4) 和父组件紧密耦合的子组件应该以父组件名作为前缀命名</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |-TodoList.vue
                        |- TodoListItem.vue
                        |- TodoListItemButton.vue
                        |- UserProfileOptions.vue （完整单词）
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code>
                    components/
                        |- TodoList.vue
                        |- TodoItem.vue
                        |- TodoButton.vue
                        |- UProfOpts.vue （使用了缩写）
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">5) 组件的 data 必须是一个函数</div>
        <div class="cs-t4 txt-indent95">当在组件中使用 data 属性的时候 (除了 new Vue 外的任何地方)，它的值必须是返回一个对象的函数。 因为如果直接是一个对象的话，子组件之间的属性值会互相影响。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    export default {
                      data () {
                        return {
                          name: 'jack'
                        }
                      }
                    }
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code>
                    export default {
                      data: {
                        name: 'jack'
                      }
                    }
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">6) Prop 定义应该尽量详细</div>
        <div class="cs-t4 txt-indent95">必须使用 camelCase 驼峰命名</div>
        <div class="cs-t4 txt-indent95">必须指定类型</div>
        <div class="cs-t4 txt-indent95">必须加上注释，表明其含义</div>
        <div class="cs-t4 txt-indent95">必须加上 required 或者 default，两者二选其一</div>
        <div class="cs-t4 txt-indent95">如果有业务需要，必须加上 validator 验证</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code>
                    props: {
                        // 组件状态，用于控制组件的颜色
                        status: {
                            type: String,
                            required: true,
                            validator: function (value) {
                            return [
                                'succ',
                                'info',
                                'error'
                            ].indexOf(value) !== -1
                        },
                        // 用户级别，用于显示皇冠个数
                        userLevel：{
                            type: String,
                            required: true
                        }
                    }
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">7) 为组件样式设置作用域</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="html">
                    &lt;template&gt;
                      &lt;button class="btn btn-close"&gt;X&lt;/button&gt;
                    &lt;/template&gt;
                    &lt;!-- 使用 `scoped` 特性 --&gt;
                    &lt;style scoped&gt;
                      .btn-close {
                          background-color: red;
                      }
                    &lt;/style&gt;
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code class="html">
                    &lt;template&gt;
                      &lt;button class="btn btn-close">X&lt;/button&gt;
                    &lt;/template&gt;
                    &lt;!-- 没有使用 `scoped` 特性 --&gt;
                    &lt;style&gt;
                      .btn-close {
                          background-color: red;
                      }
                    &lt;/style&gt;
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">8) 如果特性元素较多，应该主动换行。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="html">
                    &lt;MyComponent foo="a" bar="b" baz="c"
                                 foo="a" bar="b" baz="c"
                                 foo="a" bar="b" baz="c"
                    /&gt;
                </code>
            </pre>
        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code class="html">
                    &lt;MyComponent foo="a" bar="b" baz="c" foo="a" bar="b" baz="c" foo="a" bar="b" baz="c" foo="a" bar="b" baz="c"/&gt;
                </code>
            </pre>
        </div>

        <div class="cs-t3 txt-indent55">2.1.2 模板中使用简单的表达式</div>
        <div class="cs-t4 txt-indent95">组件模板应该只包含简单的表达式，复杂的表达式则应该重构为计算属性或方法。复杂表达式会让你的模板变得不那么声明式。我们应该尽量描述应该出现的是什么，而非如何计算那个值。而且计算属性和方法使得代码可以重用。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    &lt;template&gt;
                      &lt;p&gt;{{ 'normalizedFullName' }}&lt;/p&gt;
                    &lt;/template&gt;

                    computed: {
                      normalizedFullName: function () {
                        return this.fullName.split(' ').map(function (word) {
                          return word[0].toUpperCase() + word.slice(1)
                        }).join(' ')
                      }
                    }
                </code>
            </pre>

        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">

                      &lt;p&gt;
                              fullName.split(' ').map(function (word) {
                                  return word[0].toUpperCase() + word.slice(1)
                              }).join(' ')
                      &lt;/p&gt;

                </code>
            </pre>
        </div>

        <div class="cs-t3 txt-indent55">2.1.3 指令都使用缩写形式</div>
        <div class="cs-t4 txt-indent95">指令推荐都使用缩写形式，(用 : 表示 v-bind: 、用 @ 表示 v-on: 和用 # 表示 v-slot:)</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    &lt;input
                        @input="onInput"
                        @focus="onFocus"
                    &gt;
                </code>
            </pre>

        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    &lt;input
                        v-on:input="onInput"
                        @focus="onFocus"
                    &gt;
                </code>
            </pre>
        </div>

        <div class="cs-t3 txt-indent55">2.1.4 标签顺序保持一致</div>
        <div class="cs-t4 txt-indent95">单文件组件应该总是让标签顺序保持为</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    &lt;template&gt;...&lt;/template&gt;
                    &lt;script&gt;...&lt;/script&gt;
                    &lt;style&gt;...&lt;/style&gt;
                </code>
            </pre>

        </div>
        <div class="cs-t5 txt-indent95">反例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    &lt;template&gt;...&lt;/template&gt;
                    &lt;style&gt;...&lt;/style&gt;
                    &lt;script&gt;...&lt;/script&gt;
                </code>
            </pre>
        </div>
        <div class="cs-t3 txt-indent55">2.1.5 必须为 v-for 设置键值 key</div>
        <div class="cs-t3 txt-indent55">2.1.6 v-show 与 v-if 选择</div>
        <div class="cs-t4 txt-indent95">如果运行时，需要非常频繁地切换，使用 v-show ；如果在运行时，条件很少改变，使用 v-if。</div>

        <div class="cs-t3 txt-indent55">2.1.7 script 标签内部结构顺序</div>
        <div class="cs-t4 txt-indent95">components > props > data > computed > watch > filter > 钩子函数（钩子函数按其执行顺序） > methods</div>

        <div class="cs-t3 txt-indent55">2.1.8 Vue Router 规范</div>
        <div class="cs-t4 txt-indent95">1) 页面跳转数据传递使用路由参数</div>
        <div class="cs-t4 txt-indent95">页面跳转，例如 A 页面跳转到 B 页面，需要将 A 页面的数据传递到 B 页面，推荐使用 路由参数进行传参，而不是将需要传递的数据保存 vuex，然后在 B 页面取出 vuex 的数据，因为如果在 B 页面刷新会导致 vuex 数据丢失，导致 B 页面无法正常显示数据。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    let id = ' 123';
                    this.$router.push({ name: 'userCenter', query: { id: id } });
                </code>
            </pre>
        </div>
        <div class="cs-t4 txt-indent95">2) 使用路由懒加载（延迟加载）机制</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                     {
                        path: '/uploadAttachment',
                        name: 'uploadAttachment',
                        meta: {
                          title: '上传附件'
                        },
                        component: () => import('@/view/components/uploadAttachment/index.vue')
                     },
                </code>
            </pre>
        </div>
        <div class="cs-t4 txt-indent95">3) router 中的命名规范</div>
        <div class="cs-t4 txt-indent95">path、childrenPoints 命名规范采用kebabCase命名规范（尽量vue文件的目录结构保持一致，因为目录、文件名都是kebab-case，这样很方便找到对应的文件）</div>
        <div class="cs-t4 txt-indent95">name 命名规范采用kebabCase命名规范。</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    export const reload = [
                    {
                        path: '/reload',
                        name: 'reload',
                        component: Main,
                        meta: {
                            title: '动态加载',
                            icon: 'icon iconfont'
                        }
                    },
                    children: [
                        {
                            path: '/reload/smartReloadList',
                            name: 'smartReloadList',
                            meta: {
                                title: 'SmartReload',
                                childrenPoints: [
                                    {
                                        title: '查询',
                                    },
                                ]
                            },
                            component: () =>
                                import('@/views/reload/smartReload/SmartReload-list.vue')
                            }
                        }
                    ];
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">4) router 中的 path 命名规范</div>
        <div class="cs-t4 txt-indent95">path除了采用kebabCase命名规范以外，必须以 / 开头，即使是children里的path也要以 / 开头。如下示例</div>
        <div class="cs-t4 txt-indent95">目的：</div>
        <div class="cs-t4 txt-indent95">经常有这样的场景：某个页面有问题，要立刻找到这个vue文件，如果不用以/开头，path为parent和children组成的，可能经常需要在router文件里搜索多次才能找到，而如果以/开头，则能立刻搜索到对应的组件</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    {
                        path: '/file',
                        name: 'File',
                        component: Main,
                        children: [
                            {
                                path: '/file/fileList',
                                name: 'FileList',
                                component: () => import('@/views/file/fileList.vue')
                            },
                        ]
                    }
                </code>
            </pre>
        </div>

        <div class="cs-t2 txt-indent20">(二) Vue 项目目录规范</div>
        <div class="cs-t3 txt-indent55">2.2.1 基础</div>
        <div class="cs-t4 txt-indent95">vue 项目中的所有命名一定要与后端命名统一。</div>
        <div class="cs-t4 txt-indent95">比如权限：后端 privilege, 前端无论 router , store, api 等都必须使用 privielege 单词！</div>

        <div class="cs-t3 txt-indent55">2.2.2 使用 Vue-cli 脚手架</div>
        <div class="cs-t4 txt-indent95">使用 vue-cli2+ 来初始化项目，项目名按照上面的命名规范。</div>

        <div class="cs-t3 txt-indent55">2.2.3 目录说明</div>
        <div class="cs-t4 txt-indent95">目录名按照上面的命名规范，其中 components 组件用大写驼峰，其余除 components 组件目录外的</div>
        <div class="cs-t4 txt-indent95">所有目录均使用 kebab-case 命名。</div>
        <div class="cs-t5 txt-indent95">src                               源码目录</div>
        <div class="cs-t5 txt-indent95">|-- api                              所有api接口</div>
        <div class="cs-t5 txt-indent95">|-- assets                           静态资源，images, icons, styles等</div>
        <div class="cs-t5 txt-indent95">|-- components                       公用组件</div>
        <div class="cs-t5 txt-indent95">|-- config                           配置信息</div>
        <div class="cs-t5 txt-indent95">|-- constants                        常量信息，项目所有Enum, 全局常量等</div>
        <div class="cs-t5 txt-indent95">|-- directives                       自定义指令</div>
        <div class="cs-t5 txt-indent95">|-- filters                          过滤器，全局工具</div>
        <div class="cs-t5 txt-indent95">|-- datas                            模拟数据，临时存放</div>
        <div class="cs-t5 txt-indent95">|-- lib                              外部引用的插件存放及修改文件</div>
        <div class="cs-t5 txt-indent95">|-- mock                             模拟接口，临时存放</div>
        <div class="cs-t5 txt-indent95">|-- plugins                          插件，全局使用</div>
        <div class="cs-t5 txt-indent95">|-- router                           路由，统一管理</div>
        <div class="cs-t5 txt-indent95">|-- store                            vuex, 统一管理</div>
        <div class="cs-t5 txt-indent95">|-- themes                           自定义样式主题</div>
        <div class="cs-t5 txt-indent95">|-- views                            视图目录</div>
        <div class="cs-t5 txt-indent95">|   |-- role                             role模块名</div>
        <div class="cs-t5 txt-indent95">|   |-- |-- role-list.vue                    role列表页面</div>
        <div class="cs-t5 txt-indent95">|   |-- |-- role-add.vue                     role新建页面</div>
        <div class="cs-t5 txt-indent95">|   |-- |-- role-update.vue                  role更新页面</div>
        <div class="cs-t5 txt-indent95">|   |-- |-- index.less                      role模块样式</div>
        <div class="cs-t5 txt-indent95">|   |-- |-- components                      role模块通用组件文件夹</div>
        <div class="cs-t5 txt-indent95">|   |-- employee                         employee模块</div>

        <div class="cs-t4 txt-indent95">1) api 目录</div>
        <div class="cs-t4 txt-indent95">文件、变量命名要与后端保持一致。</div>
        <div class="cs-t4 txt-indent95">此目录对应后端 API 接口，按照后端一个 controller 一个 api js 文件。若项目较大时，可以按照业务划分子目录，并与后端保持一致</div>
        <div class="cs-t4 txt-indent95">api 中的方法名字要与后端 api url 尽量保持语义高度一致性。</div>
        <div class="cs-t4 txt-indent95">对于 api 中的每个方法要添加注释，注释与后端 swagger 文档保持一致。</div>
        <div class="cs-t5 txt-indent95">正例：</div>
        <div class="cs-t5 txt-indent95">后端 url： EmployeeController.java</div>
        <div class="cs-t5 txt-indent95">/employee/add</div>
        <div class="cs-t5 txt-indent95">/employee/delete/{id}</div>
        <div class="cs-t5 txt-indent95">/employee/update</div>
        <div class="cs-t5 txt-indent95">前端： employee.js</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    // 添加员工
                    addEmployee: (data) => {
                        return postAxios('/employee/add', data)
                    },
                    // 更新员工信息
                    updateEmployee: (data) => {
                        return postAxios('/employee/update', data)
                    },
                    // 删除员工
                    deleteEmployee: (employeeId) => {
                        return postAxios('/employee/delete/' + employeeId)
                    },
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">2) assets 目录</div>
        <div class="cs-t4 txt-indent95">assets 为静态资源，里面存放 images, styles, icons 等静态资源，静态资源命名格式为 kebab-case</div>
        <div class="cs-t5 txt-indent95">|assets</div>
        <div class="cs-t5 txt-indent95">|-- icons</div>
        <div class="cs-t5 txt-indent95">|-- images</div>
        <div class="cs-t5 txt-indent95">|   |-- background-color.png</div>
        <div class="cs-t5 txt-indent95">|   |-- upload-header.png</div>
        <div class="cs-t5 txt-indent95">|-- styles</div>

        <div class="cs-t4 txt-indent95">3) components 目录</div>
        <div class="cs-t4 txt-indent95">此目录应按照组件进行目录划分，目录命名为 KebabCase，组件命名规则也为 KebabCase</div>
        <div class="cs-t5 txt-indent95">|components</div>
        <div class="cs-t5 txt-indent95">|-- error-log</div>
        <div class="cs-t5 txt-indent95">|   |-- index.vue</div>
        <div class="cs-t5 txt-indent95">|   |-- index.less</div>
        <div class="cs-t5 txt-indent95">|-- markdown-editor</div>
        <div class="cs-t5 txt-indent95">|   |-- index.vue</div>
        <div class="cs-t5 txt-indent95">|   |-- index.js</div>
        <div class="cs-t5 txt-indent95">|-- kebab-case</div>

        <div class="cs-t4 txt-indent95">4) constants 目录</div>
        <div class="cs-t4 txt-indent95">此目录存放项目所有常量，如果常量在 vue 中使用，请使用 vue-enum 插件(https://www.npmjs.com/package/vue-enum)</div>
        <div class="cs-t4 txt-indent95">目录结构：</div>
        <div class="cs-t5 txt-indent95">|constants</div>
        <div class="cs-t5 txt-indent95">|-- index.js</div>
        <div class="cs-t5 txt-indent95">|-- role.js</div>
        <div class="cs-t5 txt-indent95">|-- employee.js</div>
        <div class="cs-t5 txt-indent95">例：</div>
        <div v-highlight>
            <pre>
                <code class="hljs">
                    export const EMPLOYEE_STATUS = {
                      NORMAL: {
                        value: 1,
                        desc: '正常'
                      },
                      DISABLED: {
                        value: 1,
                        desc: '禁用'
                      },
                      DELETED: {
                        value: 2,
                        desc: '已删除'
                      }
                    };

                    export const EMPLOYEE_ACCOUNT_TYPE = {
                      QQ: {
                        value: 1,
                        desc: 'QQ登录'
                      },
                      WECHAT: {
                        value: 2,
                        desc: '微信登录'
                      },
                      DINGDING: {
                        value: 3,
                        desc: '钉钉登录'
                      },
                      USERNAME: {
                        value: 4,
                        desc: '用户名密码登录'
                      }
                    };

                    export default {
                      EMPLOYEE_STATUS,
                      EMPLOYEE_ACCOUNT_TYPE
                    };
                </code>
            </pre>
        </div>

        <div class="cs-t4 txt-indent95">5) router 与 store 目录</div>
        <div class="cs-t4 txt-indent95">这两个目录一定要将业务进行拆分，不能放到一个 js 文件里</div>
        <div class="cs-t4 txt-indent95">router 尽量按照 views 中的结构保持一致</div>
        <div class="cs-t4 txt-indent95">store 按照业务进行拆分不同的 js 文件</div>

        <div class="cs-t4 txt-indent95">6) views 目录</div>
        <div class="cs-t4 txt-indent95">命名要与后端、router、api 等保持一致</div>
        <div class="cs-t4 txt-indent95">components 中组件要使用 PascalCase 规则</div>
        <div class="cs-t5 txt-indent95">|-- views                            视图目录</div>
        <div class="cs-t5 txt-indent95">|   |-- role                             role模块名</div>
        <div class="cs-t5 txt-indent95">|   |   |-- RoleList.vue                    role列表页面</div>
        <div class="cs-t5 txt-indent95">|   |   |-- RoleAdd.vue                     role新建页面</div>
        <div class="cs-t5 txt-indent95">|   |   |-- RoleUpdate.vue                  role更新页面</div>
        <div class="cs-t5 txt-indent95">|   |   |-- index.less                      role模块样式</div>
        <div class="cs-t5 txt-indent95">|   |   |-- components                      role模块通用组件文件夹</div>
        <div class="cs-t5 txt-indent95">|   |   |   |-- RoleHeader.vue                        role头部组件</div>
        <div class="cs-t5 txt-indent95">|   |   |   |-- RoleModal.vue                         role弹出框组件</div>

        <div class="cs-t3 txt-indent55">2.2.4 注释说明</div>
        <div class="cs-t4 txt-indent95">整理必须加注释的地方</div>
        <div class="cs-t4 txt-indent95">公共组件使用说明</div>
        <div class="cs-t4 txt-indent95">api 目录的接口 js 文件必须加注释</div>
        <div class="cs-t4 txt-indent95">store 中的 state, mutation, action 等必须加注释</div>
        <div class="cs-t4 txt-indent95">vue 文件中的 template 必须加注释，若文件较大添加 start end 注释</div>
        <div class="cs-t4 txt-indent95">vue 文件的 methods，每个 method 必须添加注释</div>
        <div class="cs-t4 txt-indent95">vue 文件的 data, 非常见单词要加注释</div>

        <div class="cs-t3 txt-indent55">2.2.5 其他</div>
        <div class="cs-t4 txt-indent95">1) 尽量不要手动操作 DOM</div>
        <div class="cs-t4 txt-indent95">因使用 vue 框架，所以在项目开发中尽量使用 vue 的数据驱动更新 DOM，尽量（不到万不得已）不要手动操作 DOM，包括：增删改 dom 元素、以及更改样式、添加事件等。</div>
        <div class="cs-t4 txt-indent95">2) 删除无用代码</div>
        <div class="cs-t4 txt-indent95">因使用了 git/svn 等代码版本工具，对于无用代码必须及时删除，例如：一些调试的 console 语句、无用的弃用功能代码。</div>
    </div>
</template>

<script>
    export default {
        name: 'QuickStart',
        data() {
            return {

            }
        }
    }
</script>
